<template>
  <div class="clearfix">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button size="small" type="warning" @click="downloadLog">
        <i class="fa fa-download"></i> {{$t('common.download')}}</el-button>
    </div>
    <el-form :model="batchForm" ref="batchForm" label-width="85px" class="demo-ruleForm" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="起始日期：" prop="startTime">
            <el-date-picker type="datetime" :placeholder="$t('common.selectDate')" v-model="batchForm.startTime" format="yyyy-MM-dd HH:mm:ss" style="width: 100%;"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="终止日期：" prop="endTime">
            <el-date-picker type="datetime" :placeholder="$t('common.selectDate')" v-model="batchForm.endTime" format="yyyy-MM-dd HH:mm:ss" style="width: 100%;"></el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="Job名称：" prop="jobId">
            <el-input v-model.trim="batchForm.jobId"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="Task名称：" prop="jobTaskId">
            <el-input v-model.trim="batchForm.jobTaskId"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="日志级别：" label-width="82px" prop="logLevel">
            <el-select v-model="batchForm.logLevel" :placeholder="$t('common.select')" style="width: 100%;">
              <el-option value="" label="请选择"></el-option>
              <el-option v-for="item in levelList" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-form-item label="关键字：" prop="keyWord">
            <el-input v-model.trim="batchForm.keyWord"></el-input>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4" class="label-lang">
          <el-form-item label="上次系统运行日期：" label-width="120px" prop="lastRunDate">
            <el-date-picker v-model="batchForm.lastRunDate" type="date" value-format="yyyyMMdd" placeholder="选择日期">
            </el-date-picker>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button size="small" type="primary" icon="el-icon-search" @click="searchLog('search')">{{$t('common.search')}}</el-button>
          <el-button size="small" type="warning" @click="resetForm('batchForm')">
            <i class="fa fa-refresh"></i> {{$t('common.reset')}}</el-button>
        </el-col>
      </el-row>
    </el-form>
    <br/>
    <gl-scroll-load :options="options" @scrollLoadData="scrollLoadData">
      <pre slot="main" v-html="logData.data"></pre>
      <div class="log-loading" slot="loading" v-loading="loading" element-loading-text="拼命加载中" element-loading-spinner="el-icon-loading" element-loading-background="rgba(0, 0, 0, 0)"></div>
      <div class="load-last" v-if="currentPage === logData.totalPage" slot="footer">已经到最后一页了~</div>
    </gl-scroll-load>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'

export default {
  data() {
    return {
      options: {
        maxHeight: '450px'
      },
      loading: false,
      currentPage: 1,
      pageSize: 10,
      batchForm: {
        startTime: '',
        endTime: '',
        jobId: '',
        lastRunDate: '',
        jobTaskId: '',
        logLevel: '',
        keyWord: ''
      },
      paramsForm: {},
      levelList: [{
        value: 'ERROR',
        label: 'ERROR'
      }, {
        value: 'WARN',
        label: 'WARN'
      }, {
        value: 'DEBUG',
        label: 'DEBUG'
      }, {
        value: 'INFO',
        label: 'INFO'
      }]
    }
  },
  activated() {
    this.searchLog()
  },
  deactivated() {
    this.currentPage = 1
  },
  methods: {
    ...mapActions('log', ['searchLogData', 'exportLog']),
    resetForm(formName) {
      this.$refs[formName].resetFields()
    },
    handleSizeChange(val) {
      const vm = this
      vm.pageSize = val
      vm.searchLog()
    },
    handleCurrentChange(val) {
      const vm = this
      vm.currentPage = val
      vm.searchLog()
    },
    downloadLog() {
      const vm = this
      vm.exportLog(vm)
    },
    searchLog(search) {
      const vm = this
      if (search) {
        vm.currentPage = 1
      }
      vm.paramsForm = Object.assign({
        tranMode: 'galaxy-batch',
        pageNo: vm.currentPage,
        pageSize: vm.pageSize
      }, vm.batchForm)
      vm.searchLogData(vm)
    },
    scrollLoadData() {
      const vm = this
      if (!vm.loading && vm.currentPage < vm.logData.totalPage) {
        vm.currentPage++
        vm.searchLog()
      }
    }
  },
  computed: {
    ...mapState({
      logData: state => state.log.logData
    })
  }
}
</script>

<style lang="scss" scoped>
@import "../../assets/common.scss";
</style>



